import React from "react"
// import { OptionsStyled } from "./Options.styled"
import { FontAwesomeIcon as FA } from "@fortawesome/react-fontawesome"
import { faCaretUp, faCaretDown } from "@fortawesome/pro-solid-svg-icons"

const OptionsToggle = function (props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that
  /*
   * View
   */
  return (
    <div className="option clickable toggle-more-options" onClick={that.toggleOptions}>
      {that.state.showOptions ? (
        <>
          Hide options &nbsp;
          <FA icon={faCaretUp} />
        </>
      ) : (
        <>
          <span className="">more &nbsp;</span>
          <FA icon={faCaretDown} />
          {/*<FA className="faCaretDown" icon={faCaretDown} />*/}
        </>
      )}
    </div>
  )
}

export default OptionsToggle
